	module.exports = {"data":{"markdownRemark":{"html":"<p>またも昔のログを見つけてしまいましたので2000年～2002年辺りを中心に更新履歴の暗黒時代となっている部分を埋めてみたついでにごっそり整理。 それにしてもこんなん作ってたんだなぁ俺……超なつかしいなぁ……。そういえばウチにまだ残っている同人誌で一番古いのが、同じくこれの二次創作なギャグ本だったりする。(ちなみに処分済みも含めて一番古いのとなると97年に買ったFINAL FANTASYのやたら分厚いアンソロコミックだった覚えがある) 東方弾幕風フレームワークライブラリ『<a href=\"/tag/flan\">Framework of LunAtic Notation</a>』のバージョン0.1iをリリースしました。今回はエフェクトオブジェクトや定型文エラーを作成する機能、また文字列整形機能など7個の関数を追加しました。</p>\n<h2>(拍)～全難易度のスペカの難易度は特に下げなくてもいいと思うんです。むしろ道中をって意味でした。～</h2>\n<p>それなら分かります。ちょっといくらなんでも打ち返し弾多すぎだろって 'Ａ｀</p>\n<h2>(拍)～夢終劇、ちょっと打ち返しが多すぎます。こんなに打ち返しばかりのSTGなんて見たことないです。</h2>\n<p>っ (グラディウス)</p>","frontmatter":{"cover":null,"date":"2006-09-02T08:25:00+09:00","strDate":"2006/9/1","title":"発掘"}}},"pathContext":{"path":"/ja/blog/2006/09/02-発掘/","langKey":"ja"}}

